@import url('https://fonts.googleapis.com/css?family=Ubuntu:400,700&display=swap&subset=cyrillic');
@import url('https://fonts.googleapis.com/css?family=Lato:400,700&display=swap');

body {
    width: 100%;
    margin: 0;
    margin-top: 0;
    background-color: #f9f9fa;
}

a {
    color: black;
}

.center {
    display: table;
    margin: 0 auto;
}

.home-container {
    margin-top: 8%;
}
.home-menu {
    margin-top: 60px;
    font-family: 'Ubuntu', sans-serif;
    font-size: 24px;
    color: #282828;
    list-style-type: none;
    margin-left: 0px;
    padding-left: 0px;
}

.home-menu > li > a > div {
    /* text-transform: uppercase; */
    background-color: #282828;
    margin-bottom: 10px;
    color: #f9f9fa;
    padding: 10px 5px;
    border-bottom: 3px solid #24abbd;
}

.home-menu > li > a > div:hover {
    background-color: #151515;
    border-bottom: 3px solid #00f2ff;
}

.no-underline {
    text-decoration: none;
}

.white-text {
    color: #f9f9fa;
}

.punkowl {
    padding: 5px 15px;
    background-color: #282828;
    color: #ffffff;
    font-family: 'Lato', sans-serif;
    font-size: 48px;
    font-weight: bold;
}

.sml {
    font-size: 28px;
    padding-left: 30px;
    border-bottom: 2px solid #24abbd;
}

.head-path{
    font-family: 'Ubuntu', sans-serif;
    font-weight: 500;
    font-size: 20px;
}

.gradient-bg-prj {
    background: #000000;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #434343, #000000);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #434343, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.header-space {
    height: 100px;
    width: 100%;
    background: #000000;
    /* font-family: 'Courier New', Courier, monospace; */
    font-family: 'Ubuntu', sans-serif;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header-text{
    color: #4c6169;
    font-weight: normal;
    font-size: 18px;
}

.project-container {
    display: flex;
    flex-wrap: wrap;
    font-family: 'Ubuntu', sans-serif;
    margin-top: 10px;
}

.project-item {
    background-color: #282828;
    color: #f9f9f9;
    width: 350px;
    margin: 10px;
    padding: 10px;
    border-radius: 4px;
    -webkit-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.50);
    -moz-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.50);
    box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.50);
}

.project-item:hover{
    background-color: #151515;
}

.project-title {
    margin-top: 150px;
    font-size: 17px;
    letter-spacing: 1px;
}

.project-desc {
    margin-top: 10px;
    font-size: 13px;
}

.project-actions {
    padding-top: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
}

.project-btn {
    background-color: #24abbd;
    color: white;
    font-weight: bold;
    border: 0px solid #fff;
    font-size: 14px;
    padding: 10px 15px;
    margin-top: 5px;
    margin-left: 5px;
    text-transform: uppercase;
}

.btn-disabled {
    background-color: #777779;
}

.punkowl-subtitle {
    font-family: 'Lato', sans-serif;
    border: 3px solid #282828;
    padding: 3px;
    padding-left: 15px;
    padding-right: 15px;
}

.language-container {
    font-family: 'Ubuntu', sans-serif;
    font-size: 16px;
    padding-top: 15px;
    padding-left: 15px;
}

.about-container {
    margin: auto;
    max-width: 900px;
    padding: 20px;
    font-family: 'Ubuntu', sans-serif;
}

.footer-container {
    margin-top: 110px;
    padding: 5px;
    background-color: transparent;
    font-family: 'Ubuntu', sans-serif;
}

.footer-content {
    text-align: center;
    line-height: 25px;
}

hr {
    border: 0;
    height: 1px;
    background: #333;
    background-image: linear-gradient(to right, #ccc, #333, #ccc);
}

.members-container {
    display: flex;
    flex-wrap: wrap;
}

.member-container {
    max-width: 180px;
    text-align: center;
    font-size: 14px;
    margin-right: 3px;
    margin-bottom: 3px;
    padding: 2px;
}

.about-text {
    font-size: 16px;
}

.grat-name {
    font-size: 16px;
}

.grat-text {
    margin-left: 3px;
    font-size: 13px;
    padding: 2px;
    background-color: #b3e096;
}

.timeline-block {
    margin-top: 50px;
}

/* Cursor Styling */
.cursor::after {
    content:'';
    display:inline-block;
    margin-left:3px;
    background-color:white;
    animation-name:blink;
    animation-duration:0.5s;
    animation-iteration-count: infinite;
  }
  h1.cursor::after {
    height:24px;
    width:13px;
  }
  p.cursor::after {
    height:13px;
    width:6px;
  }
  
  @keyframes blink {
    0% {
      opacity:1;
    }
    49% {
      opacity:1;
    }
    50% {
      opacity:0;
    }
    100% {
      opacity:0;
    }
  }